header {
  width: 100%;
}
header nav {
  padding: 20px 0px;
}
header nav ul {
  display: flex;
  justify-content: right;
  align-items: center;
}
@media (max-width: 991px) {
  header nav ul {
    flex-direction: column;
    margin-top: 10px;
    width: 100%;
  }
}
header nav ul li {
  padding: 0px 30px;
}
@media (max-width: 991px) {
  header nav ul li {
    padding-bottom: 5px;
    width: 100%;
    padding-left: 5px;
    padding-right: 5px;
    margin: 5px 0px;
  }
}
header nav ul li a {
  font-size: 14px;
  font-weight: 400;
  line-height: 22px;
  color: #081158;
}
header nav ul button {
  font-size: 14px;
  font-weight: 400;
  line-height: 22px;
  letter-spacing: 2px;
  text-align: center;
  padding: 12px 20px;
  border: none;
  border-radius: 6px;
  color: white;
  background-color: #081158;
}
header nav ul button:hover {
  cursor: pointer;
}
@media (max-width: 991px) {
  header nav ul button {
    width: 100%;
    border-radius: 0px;
  }
}
header nav ul img {
  margin-left: 25px;
}
@media (max-width: 991px) {
  header nav ul img {
    width: 5%;
    margin-top: 20px;
  }
}
header nav .bars {
  width: 30px;
  height: 30px;
  display: flex;
  justify-content: center;
  align-items: center;
}
header nav .bars:hover {
  border-color: #081158;
  color: #081158;
}
header nav .bars .fa {
  font-size: 26px;
}
header nav .headerLogin {
  position: fixed;
  top: 0px;
  left: 0px;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.96);
  z-index: 11111;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: none;
}
header nav .headerLogin h3 {
  font-size: 35px;
  font-weight: 700;
  color: #01285f;
  letter-spacing: 2px;
  margin-bottom: 30px;
}
@media (max-width: 991px) {
  header nav .headerLogin h3 {
    font-size: 30px;
    font-weight: 500;
    margin-bottom: 20px;
  }
}
@media (max-width: 767px) {
  header nav .headerLogin h3 {
    font-size: 25px;
    font-weight: 500;
    margin-bottom: 20px;
  }
}
header nav .headerLogin form {
  text-align: center;
}
header nav .headerLogin form input {
  width: 300px;
  margin: 20px 0px;
  padding: 10px 20px;
  border-radius: 5px;
  border: 2px solid #01285f;
  background-color: transparent;
  color: white;
  font-size: 14px;
  font-weight: 500;
  letter-spacing: 1px;
}
header nav .headerLogin form input:focus {
  outline: none;
}
header nav .headerLogin form input::placeholder {
  color: white;
  text-transform: capitalize;
  font-size: 13px;
  letter-spacing: 2px;
}
header nav .headerLogin form .pass {
  position: relative;
}
header nav .headerLogin form .pass .fa {
  position: absolute;
  top: 50%;
  right: 10px;
  transform: translateY(-50%);
  color: #01285f;
  font-size: 14px;
}
header nav .headerLogin form button {
  padding: 10px 30px;
  color: white;
  background-color: transparent;
  outline: 2px solid #01285f;
  border: none;
  font-size: 14px;
  font-weight: 500;
  letter-spacing: 2px;
  border-radius: 5px;
  overflow: hidden;
  position: relative;
}
header nav .headerLogin form button::after {
  content: "";
  display: block;
  width: 50%;
  height: 100%;
  position: absolute;
  top: 0px;
  right: 0px;
  background-color: rgba(1, 40, 95, 0.5);
  transform: translateX(100px);
  z-index: -11;
}
header nav .headerLogin form button::before {
  content: "";
  display: block;
  width: 50%;
  height: 100%;
  position: absolute;
  top: 0px;
  left: 0px;
  background-color: rgba(1, 40, 95, 0.5);
  transform: translateX(-100px);
  z-index: -11;
}
header nav .headerLogin form button:hover::after {
  transition: 1s;
  cursor: pointer;
  transform: translateX(0px);
}
header nav .headerLogin form button:hover::before {
  transition: 1s;
  cursor: pointer;
  transform: translateX(0px);
}
header nav .headerLogin .close {
  position: absolute;
  top: 10px;
  right: 30px;
  display: block;
  border: 2px solid #01285f;
  padding: 15px 20px;
  color: white;
  border-radius: 5px;
  font-size: 18px;
  transition: 0.3s;
  cursor: pointer;
}
header nav .headerLogin .close:hover {
  background-color: #01285f;
  transform: rotate(180deg);
}
